h2 {
	font: sans-serif;		
}
li {
	margin-top: 5px;
	text-align: left;
}

a, a:visited {
	text-decoration: none;
}

p {
	margin: 14px 0;
}

#container{
	margin: 35px 0;	
}

.container{
	width: 100%;
    margin-right: auto;
	margin-left: auto;
	position: relative;
}

.container h1{
	font-size: 38px;
}

.container h2{
	font-size: 28px;
	text-align: left;
    border-bottom: solid 1px;
}

.container h3{
	font-size: 20px;
}

.personalInformation{
	text-align: center;
    position: fixed;
    top: 0;
    left: 370px;
    display: flex;
    width: 13%;
    height: 100vh;
    padding: .5rem 1rem;
    flex-direction: column;	
	background-color:#c8edf58f;
}



.personalInformation img{
	width:200px;
	height:200px;
	border-radius: 100%;
}

.Content{
	position: absolute;
    right: 0px;
    width: 65%;
}

.Summary{
	word-wrap:break-word;
}

.demo a{
	color:blue
}

.demo div{
	margin-bottom: 40px; 
}

.demo span{
	font-weight: bold;
}

.projects p, .demo p{
	text-indent: 20px; 
}